import "./Navbar.css";
import logo from "../NBC_logo_2013.png";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

const Navbar = () => {
  return (
    <nav className="display background">
      <div className="nbc">
        <img src={logo} width="50" alt="logo" className="logo" />
        <h6 className="text">NBC NEWS</h6>
      </div>
      <ul className="display reel">
        <li className="padding">Corona Updates</li>
        <li className="padding">Politics</li>
        <li className="padding">Business</li>
        <li className="padding">Sports</li>
        <li className="padding">World</li>
        <li className="padding">Travel</li>
        <li className="padding">Podcasts</li>
        <FontAwesomeIcon icon="angle-double-right" className="icon" />
      </ul>
      <ul className="display search">
        <FontAwesomeIcon icon="user-alt" />
        <FontAwesomeIcon icon="search" className="icon" />
        <FontAwesomeIcon icon="align-right" className="icon" />
      </ul>
    </nav>
  );
};

export default Navbar;
