import { useState, useEffect } from "react";
import axios from "axios";
import "./Fruit.css";

const Fruit = () => {
  const [news, setNews] = useState([]);
  useEffect(() => {
    axios
      .get(
        "https://newsapi.org/v2/everything?q=tesla&from=2022-01-07&sortBy=publishedAt&apiKey=6bfa292daedd49f2940bba1587f4d82b"
      )
      .then(({ data: { articles } }) => {
        setNews(articles);
        console.log(articles);
      })
      .catch((err) => {
        console.log(err);
      });
  }, []);
  return (
    <div className="fruit">
      <img
        src={news.length > 0 && news[3].urlToImage}
        alt="urlImage"
        className="fruits"
      />
    </div>
  );
};

export default Fruit;
