import "./Location.css";

const Location = () => {
  return (
    <div className="location">
      <h6 className="news">Location News</h6>
      <form>
        <span className="get">Get Location specific News</span>
        <br />
        <input type="text" placeholder="Enter Your Location" />
        <br />
        <button>SUBMIT</button>
      </form>
    </div>
  );
};

export default Location;
