import "./Footer.css";
import logo from "../NBC_logo_2013.png";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

const Footer = () => {
  return (
    <div className="footer">
      <div className="displays move">
        <div>
          <img
            src={logo}
            width="100"
            height="70"
            alt="logo"
            className="footImage"
          />
          <p className="copyright">copyright &copy; 2020 | NBC NEWS</p>
        </div>
        <ul className="private">
          <li>Privacy Policy</li>
          <li className="privacy">Do not sell my personal info</li>
          <li className="privacy">Terms of service</li>
          <li className="privacy">nbcnews.com Site Map</li>
        </ul>
        <ul className="disp">
          <li className="padding">About</li>
          <li className="padding">Contact</li>
          <li className="padding">Careers</li>
          <li className="padding">Coupons</li>
        </ul>
      </div>
      <div className="white">
        <FontAwesomeIcon icon="wifi" className="whites" />
        <FontAwesomeIcon icon="ad" className="whites" />
        <FontAwesomeIcon icon="apple-alt" className="whites" />
        <FontAwesomeIcon icon="wifi" />
      </div>
      <span className="last"></span>
    </div>
  );
};

export default Footer;
