import { useState, useEffect } from "react";
import axios from "axios";
import "./Editors.css";

const Editor = () => {
  const [news, setNews] = useState([]);
  useEffect(() => {
    axios
      .get(
        "https://newsapi.org/v2/everything?q=tesla&from=2022-01-05&sortBy=publishedAt&apiKey=6bfa292daedd49f2940bba1587f4d82b"
      )
      .then(({ data: { articles } }) => {
        setNews(articles);
        console.log(articles);
      });
  }, []);
  return (
    <div className="editing editings">
      <div>
        <img
          src={news.length > 0 && news[5].urlToImage}
          alt=""
          className="imas"
        />
      </div>
      <div className="margins">
        <h5 className="making">{news.length > 0 && news[5].description}</h5>
        <p className="authorities">{news.length > 0 && news[5].content}</p>
      </div>
    </div>
  );
};

export default Editor;
