import { useState, useEffect } from "react";
import axios from "axios";
import "./Card.css";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

const Card3 = () => {
  const [news, setNews] = useState([]);
  useEffect(() => {
    axios
      .get(
        "https://newsapi.org/v2/everything?q=tesla&from=2022-01-07&sortBy=publishedAt&apiKey=6bfa292daedd49f2940bba1587f4d82b"
      )
      .then(({ data: { articles } }) => {
        setNews(articles);
        console.log(articles);
      });
  }, []);
  return (
    <div className="card">
      <img
        src={news.length > 0 && news[1].urlToImage}
        className="cardi"
        alt="logo"
      />
      <div className="lewis">
        <h3 className="john">{news.length > 0 && news[1].url}</h3>
        <p className="rep">{news.length > 0 && news[1].description}</p>
        <div className="hours">
          <h6 className="two">2 hours ago</h6>
          <p className="lucy">
            {news.length > 0 && news[1].author} | 4min read
          </p>
        </div>
      </div>
      <span className="line"></span>
      <div className="merge">
        <FontAwesomeIcon icon="heart" /> 28
        <FontAwesomeIcon icon="upload" className="log" /> 72
        <FontAwesomeIcon icon="bookmark" className="log" />
      </div>
    </div>
  );
};

export default Card3;
