import { useState, useEffect } from "react";
import axios from "axios";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import "./body.css";

const Body = () => {
  const [news, setNews] = useState([]);
  useEffect(() => {
    axios
      .get(
        "https://newsapi.org/v2/everything?q=tesla&from=2022-01-07&sortBy=publishedAt&apiKey=6bfa292daedd49f2940bba1587f4d82b"
      )
      .then(({ data: { articles } }) => {
        setNews(articles);
        console.log(articles);
      });
  }, []);

  return (
    <div className="trending">
      <div className="logo">
        <h6 className="trend">Trending</h6>
        <div>
          <FontAwesomeIcon icon="heart" className="logos" />
          <FontAwesomeIcon icon="upload" className="logos" />
          <FontAwesomeIcon icon="bookmark" className="logos" />
        </div>
      </div>
      <span className="cake">{news.length > 0 && news[2].description}</span>
      <p className="earlier">{news.length > 0 && news[2].content}</p>
      <div className="hours">
        <h6 className="two">
          {/* {news.length > 0 && news[2].publishedAt} */}2 hours ago
        </h6>
        <p className="lucy">
          By {news.length > 0 && news[3].author} | 4min read
        </p>
      </div>
    </div>
  );
};

export default Body;
