import { useState, useEffect } from "react";

import "./Right.css";
import axios from "axios";

const Right = () => {
  const [news, setNews] = useState([]);
  useEffect(() => {
    axios
      .get(
        "https://newsapi.org/v2/everything?q=tesla&from=2022-01-08&sortBy=publishedAt&apiKey=6bfa292daedd49f2940bba1587f4d82b"
      )
      .then(({ data: { articles } }) => {
        setNews(articles);
        console.log(articles);
      });
  }, []);
  return (
    <div className="right">
      <div className="circles">
        <span className="live">Live</span>
        <span className="circle1"></span>
        <span className="circle2"></span>
        <span className="circle3"></span>
      </div>
      <img
        src={news.length > 0 && news[8].urlToImage}
        alt="location"
        className="images"
      />
      <h2 className="beloved">{news.length > 0 && news[3].title}</h2>
    </div>
  );
};

export default Right;
